/* src/style/offers.css */

.offers-section {
  background: linear-gradient(to bottom right, #fff7f9, #fffef0);
  padding: 3rem 1rem;
  font-family: 'Segoe UI', sans-serif;
}

.offers-container {
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
}

.offers-title {
  font-size: 2.5rem;
  font-weight: 700;
  color: #b91c1c;
  margin-bottom: 2rem;
  text-shadow: 1px 1px 1px #fca5a5;
  animation: fadeIn 1.5s ease-in;
}

.offers-grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
}

.offer-box {
  background: white;
  border-radius: 1rem;
  padding: 1.5rem;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  border: 1px solid #ffe4e6;
}

.offer-box:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.08);
}

.offer-category {
  font-size: 1.3rem;
  color: #db2777;
  font-weight: 600;
  margin-bottom: 1rem;
  border-bottom: 2px dashed #f472b6;
  padding-bottom: 0.5rem;
}

.offer-list {
  list-style: none;
  padding: 0;
  text-align: left;
}

.offer-item {
  margin-bottom: 0.6rem;
  font-size: 0.95rem;
  color: #444;
  position: relative;
  padding-left: 1.5rem;
  animation: slideUp 0.5s ease forwards;
}

.offer-item::before {
  content: "🎁";
  position: absolute;
  left: 0;
}

@keyframes slideUp {
  0% {
    transform: translateY(10px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-15px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.offer-section {
  text-align: center;
  padding: 40px 20px;
  background: #fffaf5;
}

.offer-heading {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 10px;
  color: #d97706;
}

.offer-subtext {
  font-size: 1.1rem;
  color: #444;
  margin-bottom: 30px;
}

.offer-slider {
  overflow: hidden;
  white-space: nowrap;
}

.slide-track {
  display: flex;
  animation: scroll 20s linear infinite;
}

.slide {
  width: 250px;
  margin-right: 20px;
  flex-shrink: 0;
}

.slide img {
  width: 100%;
  height: auto;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  transition: transform 0.4s;
}

.slide img:hover {
  transform: scale(1.05);
}

@keyframes scroll {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}
